@import url(https://fonts.googleapis.com/css?family=Muli:400);
.body 
{
    margin:auto;
}
.Rectangle-3 {

    width: 87.5%;
    display: flex;
    flex-direction: column;
    margin-left:6.25%;
    margin-right:6.25%;
    
  }
  .Div1 {
    width: 100%;
    height: 200px;
    background-image: url(data:image/jpeg;base64,); 
    background-repeat: no-repeat;
    background-size: 100% 100%;   
  }


  .Div2 {
    display: inline-block;
    display: flex;
    width: 100%;
    height: 6vh;
    margin-top:1.3%;
    flex-direction: row;
  
  }
  .Search
{
    width:100%;
    height: 100%;   
}
  .Div3 {
    
    width: 100%;
    height: 100%;
    margin-top: 1.3%;
    display: grid;
    grid-column-gap: 2%;
    grid-row-gap:20px;
    grid-template-columns: 32% 32% 32%;
    
  }

.warning
{
  margin-top:10%;
}

@media (max-width: 550px) 
{
  .Div3{
    display: grid;
    grid-column-gap: 2%;
    grid-row-gap: 3%;
    grid-template-columns: 100%;
  }
  
  .Rectangle-2
  {
    border:2px;
    border-color: black;
    width:100%;
    margin-top: 4%;
  }
}
.bar
{
   display: flex;
}
 .Searchbox {
    width:85%;
    height: 100%;
    background-image: url(/MoviesSearch/static/media/icon.a567c220.svg); 
    background-repeat: no-repeat;
    background-size: 4%;
    background-position-x: 4%;
    background-position-y: 20%;
    background-color: white;
    text-indent:15%;
    border: solid 0.5px #4a90e2;
  }
  .button {
    width: 14%;
    height: 100%;
    background-color:#4a90e2;
    border: solid 0.5px rgba(151, 151, 151, 0.2);
  }

  .go{
        font-family: 'Muli',sans-serif;
        font-size: 100%;
        font-weight: 600;
        letter-spacing: 0.4px;
        color: #ffffff;
      
} 

.Rectangle-2
{
  position: relative;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  width:100%;
  height: 400px;
  background: radial-gradient(circle, red, yellow, green);

  
}
.Rectangle-2:hover
{
    box-shadow: 0px 0px 4px 0px #000000;
    z-index: 2;
    height: auto;
    -webkit-transition: all 200ms ease-in;
    -webkit-transform: scale(1.5);
    -ms-transition: all 200ms ease-in;
    -ms-transform: scale(1.5);
    -moz-transition: all 200ms ease-in;
    -moz-transform: scale(1.5);
    transition: all 200ms ease-in;
    -webkit-transform: scale(1.0);
    transform: scale(1.1);
    
    
}
.name {

